import React from 'react';
import { Button } from '@mui/material';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import { Grid } from '@mui/material';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import { GetUserInfoResponse } from '../assets/abi/farm-contract';

import { formatEthFromWei } from '../Formatters';



interface ContractStatsProps {
  style?: React.CSSProperties;
}

const bull = (
    <Box
      component="span"
      sx={{ display: 'inline-block', mx: '2px', transform: 'scale(0.8)' }}
    >
      •
    </Box>
  );

class ContractStats extends React.Component<ContractStatsProps> {

  state = {
    contractBalance: "",
    totalInvested: "",
    totalReinvested: ""
  }
  baseState: any;
  constructor(props: ContractStatsProps) {
      super(props);

      this.baseState = this.state;


      // properties
  }

  componentDidMount = () => { 

    this.checkState();
    setInterval(this.checkState, 5000);
  }

  componentWillUnmount = () => { 

  }

  onClick_Withdraw = () => {
    window.farm_contract.methods.withdraw().send({
      from: window.walletAddress
    }).catch((err: any) => {
        console.log(err);
    });
  }

  onClick_VerifiedContract = () => {
    let a = document.createElement("a");
    a.target = "_blank";
    a.href = "https://testnet.bscscan.com/address/"+ window.contractAddress +"#code";
    a.click();
  }

  checkState = () => {  
    if(window.connected !== true){
      this.setState(this.baseState);
      return;
    }    
    window.farm_contract.methods.totalInvested().call().then((result: string) => {
      this.setState({
        totalInvested: result
      });
    }).catch((err:any) => {
      console.log(err);
    });

    window.farm_contract.methods.getContractBalance().call().then((result: string) => {
      this.setState({
        contractBalance: result
      });
    }).catch((err:any) => {
      console.log(err);
    });

    window.farm_contract.methods.totalReInvested().call().then((result: string) => {
      this.setState({
        totalReinvested: result
      });
    }).catch((err:any) => {
      console.log(err);
    });

  }  

  render = () => {
    return (
    <Card sx={{ minWidth: 275 }}>
        <CardContent>
          <Grid container>
            <Grid item xs={12} md={6}>
              <Typography sx={{ fontSize: 16 }} color="text.secondary" gutterBottom>
                total invested
              </Typography>
              <Typography sx={{ fontSize: 20 }} color="text.primary" gutterBottom>
              {formatEthFromWei(this.state.totalInvested)} BNB
              </Typography>
            </Grid>

            <Grid item xs={12} md={6}>
              <Typography sx={{ fontSize: 16 }} color="text.secondary" gutterBottom>
                  contract balance
              </Typography>
              <Typography sx={{ fontSize: 20 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.contractBalance)} BNB
              </Typography>
            </Grid>

            <Grid item xs={12} md={6}>
              <Typography sx={{ fontSize: 16 }} color="text.secondary" gutterBottom>
                  total reinvested
              </Typography>
              <Typography sx={{ fontSize: 20 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.totalReinvested)} BNB
              </Typography>
            </Grid>

          </Grid>
        </CardContent>
        <CardActions>
          <Button sx={{my: 1}} size="small" onClick={this.onClick_VerifiedContract}>Verified Contract</Button>
        </CardActions>
    </Card>
    );
  }
}

export default ContractStats;