import React from 'react';
import logo from './logo.svg';
import './App.css';

import { styled } from '@mui/material/styles';
import { Button, Grid, Typography } from '@mui/material';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Plan from './components/Plan';
import UserStats from './components/UserStats';
import ContractStats from './components/ContractStats';
import { AbiItem } from 'ethereum-abi-types-generator';

import ConnectButton from "./components/ConnectButton";

import bnbfarm from "./assets/img/bnbfarm.png";
import bnbfarmtext from "./assets/img/bnbfarm-header-text.png";

import {ContractContext} from './assets/abi/farm-contract';
import Web3 from "web3";

//
//  Global setup Web3 Contract-Interaction
//
import abi from "./assets/abi/farm-contract.json";
const contractAddress: string = '0x0f6EBFF0d3Abc8E061656E5a34F8d70aaf687Cba';

declare global {
  interface Window {
    farm_contract: ContractContext;
    web3: Web3;
    ethereum: any;
    contractAddress: string;
    contract_abi: AbiItem[]
    walletAddress: string;
    connected: boolean;
  }
}

const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.h3,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

function App() {
  
  window.contractAddress = contractAddress;
  window.contract_abi = abi as AbiItem[];

  //  Main Layout
  //
  return (
    <Box sx={{ flexGrow: 1 }} style={{width: "80%", margin: "auto", marginTop: "32px"}}>
      <Grid container spacing={2}>
        <Grid item xs={12}>
          {/* <Item style={{textAlign: "left"}}><img style={{width: "100%"}} src={headerbg}/></Item> */}
          <Item sx={{ display: { xs: 'block', md: 'none' } }} className={"flex-middle-center"}>
            <img style={{width: "100%"}} src={bnbfarmtext}/>
          </Item>
          <Item sx={{ display: { xs: 'none', md: 'block' } }} className={"flex-middle-center"}>
            <img style={{width: "10%"}} src={bnbfarm}/>
            <img style={{width: "80%"}} src={bnbfarmtext}/>
          </Item>
        </Grid>
        <Grid item xs={12} md={4}>
          <Paper style={{padding: "16px"}}>
            <ConnectButton />
          </Paper>
        </Grid>
        <Grid item xs={12} md={4}>
          <ContractStats></ContractStats>
        </Grid>
        <Grid item xs={12} md={4}>
          <UserStats></UserStats>
        </Grid>
        <Grid item xs={12} md={3}>
          <Plan label={"Plan 1"} minInvest={0.05} percentage={23} days={7} planIndex={0}></Plan>
        </Grid>
        <Grid item xs={12} md={3}>
          <Plan label={"Plan 2"} minInvest={0.05} percentage={7} days={30} planIndex={1}></Plan>
        </Grid>
        <Grid item xs={12} md={3}>
          <Plan label={"Plan 3"} minInvest={0.05} percentage={5} days={50}  planIndex={2}></Plan>
        </Grid>
        <Grid item xs={12} md={3}>
          <Plan label={"VIP Plan"} minInvest={1.5} percentage={30} days={5}  planIndex={3}></Plan>
        </Grid>
        {/* <Grid item xs={12} md={12}>
          <PlanDynamic label={"Dynamic"} percentage={7} days={20}></PlanDynamic>
        </Grid> */}
      </Grid>
    </Box>
  );
}

export default App;
