import Web3 from 'web3';

export function formatEthFromWei (value: string)  {
    value = Web3.utils.fromWei(value);
    return new Intl.NumberFormat('en-GB').format(parseFloat(value));
    //return parseFloat(value).toFixed(5).toLocaleString();
 };

 export function formatNumber (value: any)  {
    return new Intl.NumberFormat('en-GB').format(parseFloat(value));
 };